<template>
  <div class="AdminGoods">
    <!-- 头部导航栏 -->
    <HeaderAdmin>
      <template v-slot:search>
        <div style="width:50%">
          <el-input
            style="width:40%;"
            v-model="searchInput"
            placeholder="请输入商品名称"
          ></el-input>
          <el-button style="width:20%;max-width:80px;margin-left:6%"
            >搜索</el-button
          >
          <el-button
            type="primary"
            style="width:20%;max-width:80px;margin-left:10%"
            >添加</el-button
          >
        </div>
      </template>
    </HeaderAdmin>
    <div class="AdminContent" v-loading="loading">
      <el-empty v-if="isEmpty"></el-empty>
      <GoodsCommon v-else v-for="item in goodsList" :key="item._id">
        <!-- 图片 -->
        <template v-slot:goodsImg>
          <!-- lazy 开启懒加载 可视才加载 -->
          <el-image
            style="width: 60px; height: 60px;"
            :src="item.goodsSRC"
            fit="fit"
            lazy
          ></el-image>
        </template>
        <!-- 商品信息 -->
        <template v-slot:goodsDetail>
          <h4>
            名称：{{ item.goodsName }}
            <i style="margin-left:30px">单价：{{ item.goodsPrice }}</i>
            <i style="margin-left:30px">库存：{{ item.goodsSum }}</i>
          </h4>
          <p>{{ item.goodsDetail }}</p>
        </template>
        <!-- 数量 -->
        <template v-slot:goodsBuySum> </template>
        <!-- 删除 -->
        <template v-slot:goodsBtn
          ><el-button type="danger"><b>删除</b></el-button></template
        >
        <!-- 修改 -->
        <template v-slot:goodsBuy>
          <el-button type="success"><b>修改</b></el-button>
        </template>
      </GoodsCommon>
    </div>
    <div class="block">
      <el-pagination
        background
        layout="prev, pager, next"
        :page-size="pageSize"
        :total="count"
        @current-change="getGoods"
      >
      </el-pagination>
    </div>
  </div>
</template>
<script>
// axios请求
import request from '@/utils/request.js'
import GoodsCommon from '@/components/GoodsCommon/GoodsCommon'

export default {
  data() {
    return {
      name: 'AdminGoods',
      // 搜索关键字
      searchInput: '',
      // 商品总数量
      count: 0,
      // 一页展示12条
      pageSize: 12,
      // 数据跳过几项
      Skip: 0,

      isEmpty: true,
      loading: true,
      goodsList: []
    }
  },
  components: {
    GoodsCommon
  },
  created() {
    this.getGoods()
  },
  methods: {
    // gooodsList商品数据请求
    async getGoods(pageNum) {
      this.loading = true
      if (pageNum === undefined) {
        pageNum = 1
      }
      if (pageNum < this.count / 12 + 1) {
        this.Skip = 12 * (pageNum - 1)
      }
      const { data: res } = await request.get('/getGoods', {
        params: {
          count: this.Skip
        }
      })
      this.goodsList = []
      this.goodsList = res.data
      this.count = res.goodsCount
      if (this.goodsList.length > 0) {
        this.isEmpty = false
      }
      this.loading = false
    }
  }
}
</script>
<style lang="less" scoped>
.AdminContent {
  margin-top: 2px;
  width: 1000px;
  overflow: auto;
}

.block {
  margin: 10px 0 20px 0;
}
</style>
